/*
routes - frontend link-panel

cada url corresponde a uma rota que bate lá no backend
ao obter o retorno da requisição, renderiza o componente informado
ex: rota front path="/" vai chamar a rota backend 
routes.get('/', LoginController.defaultDb);
*/

import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';

import Login from './pages/Login';
import Main from './pages/Main';
import Dashboard from './pages/Dashboard';

//posts
import Posts from './pages/Posts';
import Post from './pages/Post/Post';
import AddPost from './pages/Post/AddPost';
//end posts

//categories
import Categories from './pages/Categories';
import Category from './pages/Category/Category';
import AddCategory from './pages/Category/AddCategory';
//end categories

//authors
import Authors from './pages/Authors';
import Author from './pages/Author/Author';
import AddAuthor from './pages/Author/AddAuthor';
//end authors

import Profile from './pages/Profile';
import Clients from './pages/Clients';

//exportamos a função Routes com todos os componentes 
export default function Routes(){
  //precisa de algum jeito redirecionar para o login 
  //se nao tiver o localStorage, ainda nao deu certo assim

  return (
    <BrowserRouter>
      <Route path="/" exact component={Login} />
      <Route path="/main/:userId" exact component={Main} />
      <Route path="/main/:userId/dashboard" exact component={Dashboard} />

      {/* posts */}
      <Route path="/main/:userId/posts" exact component={Posts} />
      <Route path="/main/:userId/posts/:postId" component={Post} />
      <Route path="/main/:userId/post/new" component={AddPost} />
      {/* end posts */}

      {/* categories */}
      <Route path="/main/:userId/categories" component={Categories} />
      <Route path="/main/:userId/categories/:categoryId" component={Category} />
      <Route path="/main/:userId/category/new" component={AddCategory} />
      {/* end categories */}

      {/* authors */}
      <Route path="/main/:userId/authors" component={Authors} />
      <Route path="/main/:userId/authors/:categoryId" component={Author} />
      <Route path="/main/:userId/author/new" component={AddAuthor} />
      {/* end categories */}

      <Route path="/main/:userId/profile" component={Profile} />

      <Route path="/main/:userId/clients" component={Clients} />
    </BrowserRouter>
  )
}