/*
componente Header - frontend link-panel

header com o logotipo, barra de pesquisa e ações
*/
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import api from '../services/api';

import './Header.css';
import '../pages/Dashboard.css';

import logo from '../assets/link-logo.png';

export default class Header extends Component {
  constructor(props){
    super(props);

    this.userId = window.location.pathname.substring(6, 30);

    this.handleSearch = this.handleSearch.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
  }

  handleSearch(event){
    event.preventDefault();
    const data = new FormData(event.target);
    const value = data.get("main-search")

    var array = [];
    var links = document.getElementsByTagName("a");
    for(var i = 0, max = links.length; i < max; i++) {
      array.push(links[i].href);
    }

    var el = array.find(a => a.includes(value));
    // var el = array.filter(e => e !== value);

    console.log(value);
    console.log(array);
    console.log('el', el)
  }

  //handleLogout chama a função logout do backend
  handleLogout = async() => {
    //requisita o logout
    await api.post('/panel/logout')
    .then((res) => {       
      //se teve retorno positivo, remove o localStorage 
      //no front e redireciona para o / (login) que seta
      //o banco defaultDb novamente
      localStorage.removeItem('db');
      localStorage.clear();
      window.location.assign(`/`);
    })
    .catch((error) => {
      console.log("Error returning logout", error);
    });
  }

  render() {
    return (
      <nav className="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">

        <a className="navbar-brand col-sm-3 col-md-2 mr-0" href={`/main/${this.userId}/dashboard`}>
          <img src={logo} alt="logo" className="logo"/>
        </a>

        <form id="form-search-files" className="form-container" onSubmit={this.handleSearch}>
          <Row>
            <Col md="8">
              <input className="form-control form-control-dark" type="text" 
                placeholder="Busque opções..." aria-label="Pesquisar" name="main-search"
                style={{marginLeft: '-400px', width: '500px', height: '68px'}} />
            </Col>

            <Col md="4">
              <button id="search-files" type="submit" 
                style={{height: '68px', marginTop: '-1px', marginLeft: '-50px', opacity: '0.5'}}>
                <span className="space-icon">Pesquisar</span>                        
              </button>
            </Col>
          </Row>
        </form>

        <ul className="navbar-nav px-3">
          <li className="nav-item text-nowrap">
            <span onClick={this.handleLogout} className="nav-link">Sair</span>
          </li>
        </ul>

      </nav>
    );
  }
}
