/*
component AddAuthor - frontend link-panel

tela para adicionar uma nova categoria
*/

import React from "react";
import api from "../../services/api";

import {
  Container,
  Row,
  Col
} from "reactstrap";
import "../Default.css";

import Header from "../../components/Header";
import Sidebar from "../../components/Sidebar";
//pega o db do localStorage front
var db = localStorage.getItem("db");

export default class AddAuthor extends React.Component {
  constructor(props) {
    super(props);

    // this.timer = null
    this.state = {
      return: '',
      update: false
    };
    
    this.userId = window.location.pathname.substring(6, 30);

    this.handleSubmit = this.handleSubmit.bind(this);
    this.Cancel = this.Cancel.bind(this);
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db) {
      window.location.href = "/";
    }

    var inputDatabase = document.getElementById("inputDatabase")
    
    if (this.userId !== '5d9261850e850f140454f415'){
      inputDatabase.setAttribute("readonly", "readonly")
    }
  }


  //handleSubmit pega todos os campos do form e 
  //manda atualizar o que tiver alterado
  handleSubmit(event){
    //previne comportamento padrão de redirecionar
    event.preventDefault();
    //div com mensagem de usuario ou senha incorretos
    const alert = document.getElementById('link-alert');
    const btnSave = document.getElementById('save-author');
    const btnCancel = document.getElementById('cancel-author');
    //buscando campos do form
    var form = document.getElementById("form-add-author");
    var elements = form.elements;
    
    //const data pega o value de todos os campos do form
    const data = new FormData(event.target);
    //requisita o metodo login passando usuario e senha
    api.post(`/panel/author/new`, { 
      db,
      "name": data.get("name"),
      "description": data.get("description"),
      "database": data.get("database"),
      "userId": this.userId,
    })
    .then((res) => {  
      //se tem key msg entao nao encontrou o author
      if (res.data.error){
        this.setState({ return: res.data.error });
        //se nao encontrou, mostra o alerta
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-danger");
        //alerta some após 5 segundos
        setTimeout(function() {
          alert.style.display = "none";
        }, 5000);
      }
      else if (res.data.msg){
        this.setState({ update: true }) 
        this.setState({ return: res.data.msg });
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-success");
        btnSave.style.display = "none";
        btnCancel.style.display = "none";

        //percorre os campos do form para desabilitar apos salvar
        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        const path = window.location.pathname.substring(0, 30)
        // console.log(path)
        setTimeout(function() {
          window.location.href = path + '/authors'
        }, 3000);       
      }
    })
    .catch((error) => {
      console.log(error);
    });
  }

  //cancel ira cancelar o novo author e
  //retornar para a lista
  Cancel(){
    const btnSave = document.getElementById('save-author');
    const btnCancel = document.getElementById('cancel-author');
    var form = document.getElementById("form-add-author");
    var elements = form.elements;

    btnSave.style.display = "none";
    btnCancel.style.display = "none";

    for (var i = 0, len = elements.length; i < len; ++i) {
      elements[i].readOnly = true;
      elements[i].disabled = true;
    }

    const path = window.location.pathname.substring(0, 30)
    // console.log(path)
    window.location.href = path + '/authors'
  }


  render() {
    //pegando o db do cookie
    let db = localStorage.getItem('db')

    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="10">
              <div className="spacement" />
                <>
                
                <div id="link-alert"
                  style={{display: 'none'}} role="alert">
                  {this.state.return ? this.state.return : ''}
                </div>
                <Row>
                  <Col md="10">
                    <h3> novo autor </h3>
                  </Col>
                </Row><hr/><br/>

                <form id="form-add-author" className="form-container" onSubmit={this.handleSubmit}>
                  <Row>
                    <Col md="10">
                      <Row>
                        <Col md="4">
                          <div className="form-group row">
                            <label htmlFor="inputName" className="col-sm-4 col-form-label">
                              Nome</label>
                            <div className="col-sm-10">
                              <input type="text" className="form-control" name="name" required
                                id="inputName" placeholder="Como o autor se chama?" />
                            </div>
                          </div>
                        </Col>

                        <Col md="8">
                          <div className="form-group row">
                            <label htmlFor="inputDescription" className="col-sm-4 col-form-label">
                              Descrição</label>
                            <div className="col-sm-10">
                              <input type="text" className="form-control" name="description" required
                                id="inputDescription" placeholder="Algo pra lembrar dele(a)" />
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row>
                        <Col md="4">
                          <div className="form-group row">
                            <label htmlFor="inputDatabase" className="col-sm-4 col-form-label">
                              Base</label>
                            <div className="col-sm-10">
                              <input type="text" required className="form-control" name="database"
                                id="inputDatabase" defaultValue={db} />
                            </div>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  
                  <Row>
                    <Col md="7">
                      <button id="cancel-author" className="cancel" onClick={this.Cancel}>
                        <i className="fa fa-arrow-left" aria-hidden="true"></i>
                        <span className="space-icon">Não salvar</span>
                      </button>
                    </Col>

                    <Col md="4">
                      <button id="save-author" type="submit">
                        <i className="fa fa-check" aria-hidden="true"></i>
                        <span className="space-icon">Confirmar alterações</span>                        
                      </button>
                    </Col>
                  </Row>              
                </form>
                </>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
