/*
component Main - frontend link-panel

pode ser que nao seja necessario esse componente
no momento ele pega o usuario e banco criptografados 
na url, é mais uma re-verificação de login do que outra coisa
*/

import React from 'react';
import api from '../services/api';

import './Dashboard.css';

export default class Main extends React.Component{
  constructor(props){
    super(props);

    this.state = {
      noDb: '',
    };
  }

  componentDidMount(){
    //pega parte da url - remove 'main/' e pega o restante
    const path = window.location.pathname.substring(6);
    //decode base64 dessa string da url
    const decode = atob(path);
    const db = decode.split('_');

    //requisita o dashboard passando id e database do usuario
    api.post(`/main/${db[0]}`, {
      "id": db[0],
      "database": db[1]
    })
    .then((res) => {       
      //se retornou key msg, quer dizer que nao tem
      if (res.data.msg !== ''){
        this.setState({ noDb: res.data.msg });
      }
      //se tem id e database, seta o localStorage front com esse banco
      //e redireciona para o dashboard
      if (res.data.user.database !== '')
        localStorage.setItem('db', res.data.user.database);
        window.location.href = `/main/${res.data.user._id}/dashboard`;
    })
    .catch(function(error){
      console.log('Error getting the main:', error);
    });    
  }
  
  render(){
    
    return (
      <div>
        <h1>carregando info...</h1><br/>
        {this.state.noDb ? this.state.noDb : "só mais alguns segundos..."}
      </div>
    )
  }
}
