/*
component Profiles - frontend link-panel

aqui buscamos os dados de perfil passando o banco do usuario
como parametro, que renderiza o retorno na tabela
*/

import React from "react";
import api from "../services/api";

import {
  Container,
  Row,
  Col
} from "reactstrap";
import "./Dashboard.css";
import "./Default.css";

import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
//pega o db do localStorage front
var db = localStorage.getItem("db");

export default class Profile extends React.Component {
  constructor(props) {
    super(props);

    this.userId = window.location.pathname.substring(6, 30);
    this.state = {
      profile: [],
      name: '',
      username: '',
      email: '',
      password: '',
      domain: '',
      identity: '',
      phone: '',
      plan: '',
      expires: '',
      empty: '',
      return: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.Cancel = this.Cancel.bind(this);
    this.fetchData = this.fetchData.bind(this);
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db) {
      window.location.href = "/";
    } else {
      this.fetchData();
      // this.timer = setInterval(() => this.fetchData, 50)
    }
  }

  //handleSubmit pega todos os campos do form e 
  //manda atualizar o que tiver alterado
  handleSubmit(event){
    //previne comportamento padrão de redirecionar
    event.preventDefault();
    // console.log(userId)
    //div com mensagem de post incorreto
    const alert = document.getElementById('link-alert');
    const btnSave = document.getElementById('save-profile');
    const btnCancel = document.getElementById('cancel-profile');
    
    //buscando campos do form
    var form = document.getElementById("form-profile");
    var elements = form.elements;
    
    //const data pega o value de todos os campos do form
    const data = new FormData(event.target);
    //requisita o metodo login passando usuario e senha
    api
      .post(`/panel/clients/${this.userId}`, { 
        db,
        "email": data.get("email"),
        "username": data.get("username"),
        "password": data.get("password"),
        "identity": data.get("identity"),
        "phone": data.get("phone")
      })
      .then((res) => {       
        //se tem key msg entao nao encontrou o post
        // console.log(res)
        if (res.data.error){
          this.setState({ return: res.data.error });
          //se nao encontrou, mostra o alerta
          alert.style.display = "block";
          alert.setAttribute("class", "alert alert-danger");
          //alerta some após 5 segundos
          setTimeout(function() {
            alert.style.display = "none";
          }, 5000);
        }
        else if (res.data.msg){
          this.setState({ return: res.data.msg });
          alert.style.display = "block";
          alert.setAttribute("class", "alert alert-success");
          btnSave.style.display = "none";
          btnCancel.style.display = "none";
          
          //percorre os campos do form para desabilitar apos salvar
          for (var i = 0, len = elements.length; i < len; ++i) {
            elements[i].readOnly = true;
            elements[i].disabled = true;
          }

          const path = window.location.pathname.substring(0, 30)
          // console.log(path)
          setTimeout(function() {
            window.location.href = path + '/dashboard'
          }, 3000);       
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  handleChange(event) {
    var eventName = event.target.name

    if (eventName === 'email'){
      this.setState({
        email: event.target.value
      });
    }else if (eventName === 'username'){
      this.setState({
        username: event.target.value
      });
    }else if (eventName === 'password'){
      this.setState({
        password: event.target.value
      });
    }else if (eventName === 'domain'){
      this.setState({
        domain: event.target.value
      });
    }else if (eventName === 'identity'){
      this.setState({
        identity: event.target.value
      });
    }else if (eventName === 'phone'){
      this.setState({
        phone: event.target.value
      });
    }else if (eventName === 'plan'){
      this.setState({
        plan: event.target.value
      });
    }else if (eventName === 'expires'){
      this.setState({
        expires: event.target.value
      });
    }
  }

  Cancel(){
    const btnSave = document.getElementById('save-profile');
    const btnCancel = document.getElementById('cancel-profile');
    var form = document.getElementById("form-profile");
    var elements = form.elements;

    btnSave.style.display = "none";
    btnCancel.style.display = "none";

    for (var i = 0, len = elements.length; i < len; ++i) {
      elements[i].readOnly = true;
      elements[i].disabled = true;
    }

    const path = window.location.pathname.substring(0, 30)
    // console.log(path)
    window.location.href = path + '/dashboard'
  }

  //fetchData requisita os authorss do usuario
  fetchData() {
    // console.log(db)
    var userId = this.userId

    api
      .post("/panel/profile", { userId })
      .then(res => {
        console.log(res)
        if(res.data.error){
          this.setState({
            empty: res.data.error
          });
        }else{
          //se tem registro insere eles no state.profile
          this.setState({
            profile: res.data,
            name: res.data.name,
            email: res.data.email,
            username: res.data.username,
            password: res.data.password,
            domain: res.data.domain,
            identity: res.data.identity,
            phone: res.data.phone,
            plan: res.data.plan,
            expires: res.data.expires
          });
        }        
      })
      .catch(function(error) {
        console.log("Error getting the profile:", error);
      });
  }

  render() {
    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="8">
              <div className="spacement" />
              <h2>Minha Conta</h2><br/>
              {/* se tem perfil mostra ele na tabela */}

              {!this.state.error ? (
                <>
                  <div id="link-alert"
                    style={{display: 'none'}} role="alert">
                    {this.state.return ? this.state.return : '...' }
                  </div>

                  <div>
                    <div className="capa" style={{padding: '10px', background: "url('https://guilherme.eadplataforma.com/assets/img/default-cover.jpg') center center no-repeat", backgroundSize: "cover"}}>
                      <Row>
                        <Col md="12">
                          <Col md={{size: '3', offset:'0'}}>
                            <img src={this.state.profile.avatar} 
                            style={{width: '170px', height: '170px', borderRadius: '50%', verticalAlign: 'middle'}} className="img-circle" alt="Gabriel Santos" />
                          </Col>
                        </Col>
                      </Row>                    
                    </div><br/>

                    <h4>
                      {this.state.profile.name} | Plano {this.state.plan === 1 ? "Blog Basic" : "Site"}
                    </h4><br/>

                    <form id="form-profile" className="form-container" onSubmit={this.handleSubmit}>
                      <div className="form-row">
                        <div className="form-group col-md-6">
                          <label htmlFor="inputEmail">Email</label>
                          <input type="email" className="form-control" id="inputEmail" name="email"
                            value={this.state.email} onChange={this.handleChange} placeholder="Email" />
                        </div>
                        <div className="form-group col-md-6">
                          <label htmlFor="inputEmail">Username</label>
                          <input type="text" className="form-control" id="inputUsername" name="username"
                            value={this.state.username} onChange={this.handleChange} placeholder="Username" />
                        </div>
                      </div>

                      <div className="form-row">
                        <div className="form-group col-md-4">
                          <label htmlFor="inputPassword">Senha</label>
                          <input type="password" className="form-control" id="inputPassword" name="password" 
                            value={this.state.password} onChange={this.handleChange} placeholder="Password" />
                        </div>
                        <div className="form-group col-md-4">
                          <label htmlFor="inputDomain">Domínio</label>
                          <input type="text" className="form-control" id="inputDomain" name="domain"
                            value={this.state.domain} readOnly />
                        </div>
                        <div className="form-group col-md-4">
                          <label htmlFor="inputIdentity">Identidade</label>
                          <input type="text" className="form-control" id="inputIdentity" name="identity"
                            value={this.state.identity} onChange={this.handleChange} placeholder="CPF/CNPJ" />
                        </div>
                      </div>

                      <div className="form-row">
                        <div className="form-group col-md-6">
                          <label htmlFor="inputPhone">Telefone</label>
                          <input type="text" className="form-control" id="inputPhone" name="phone"
                            value={this.state.phone} onChange={this.handleChange} />
                        </div>
                        <div className="form-group col-md-6">
                          <label htmlFor="inputPlan">Plano</label>
                          <input type="text" className="form-control" id="inputPlan" 
                            value={this.state.plan} readOnly />
                        </div>
                      </div><br/>
                      
                      <Row>
                        <Col md="8">
                          <button id="cancel-profile" className="cancel" onClick={this.Cancel}>
                            <i className="fa fa-arrow-left" aria-hidden="true"></i>
                            <span className="space-icon">Retornar</span>
                          </button>
                        </Col>

                        <Col md="4">
                          <button id="save-profile" type="submit">
                            <i className="fa fa-check" aria-hidden="true"></i>
                            <span className="space-icon">Confirmar alterações</span>                        
                          </button>
                        </Col>
                      </Row>
                    </form><br/>
                  </div>
                </>
              ) : (
                /*se não tem mostra esse html*/
                <h1>{this.state.error} :(</h1>
              )}
            </Col>

            <Col md="2" style={{
              borderLeft: 'solid 1px #e7e7e7',
              marginTop: '5em'
            }}>              
              <div style={{position: 'fixed'}}>
                <ul className="list-group">
                  <li className="list-group-item"><h5>Ações</h5></li>
                  
                  <li className="list-group-item list-group-item-light">
                    <a mp-mode="dftl" 
                      href="https://www.mercadopago.com.br/checkout/v1/redirect?pref_id=184683014-ef1000c9-6925-42f3-a0de-6dffda966652" 
                      name="MP-payButton" className='green-tr-m-ov-none'>
                      Pagamento
                    </a>
                  </li>
                  <li className="list-group-item list-group-item-light">
                    <a href={`/main/${this.userId}/changePlan`} className="badge badge-info lg-txt">
                      <i className="fa fa-bookmark" aria-hidden="true"></i>
                      <span className="space-icon">Alterar meu plano</span>
                    </a>
                  </li>
                </ul>
              </div>              
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
