import React from 'react';
import api from '../services/api';
import { Container, Row, Col } from 'reactstrap';

import './Dashboard.css';
import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
//pega o db do localStorage front
var db = localStorage.getItem('db')

export default class Dashboard extends React.Component{
  
  constructor(props){
    super(props)

    this.timer = null;
    
    this.state = {
      posts: [],
      categories: [],
      authors: [],
      isFetching: true,
    }

    this.fetchData = this.fetchData.bind(this);
  }

  componentDidMount(){
    //se nao esta autenticado
    if (!db){
      window.location.href="/"
    }else{
      this.fetchData();
      this.timer = setInterval(() => this.fetchData(), 50);
      setTimeout(() => {
        clearInterval(this.timer);
      }, 50);
    }
  }

  fetchData(){
    
    api.post('/panel/dashboard', {
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }, db 

    })
    .then(res => {
      
      this.setState({
        posts: res.data.posts, 
        categories: res.data.categories,
        authors: res.data.authors
      })

    });
  }

  render(){

    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2"> 
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="10">
              <div className="spacement" />
              <h1>Dashboard</h1><br/>
              
              <div className="panel-body">
                <Row>
                  <div className="col-md-4 col-lg-4">
                    <div className="panel panel-link-before">
                    {/* <div className="panel" style={{background: 'linear-gradient(to right, #1ee64b, #2565db);'}}> */}
                      <div className="panel-heading text-center">
                        <strong className="panel-title">Posts</strong>
                      </div>
                      <div className="panel-body">
                        <h4 className="text-center">
                          {this.state.posts}
                        </h4>
                      </div>
                    </div>
                  </div>

                  <div className="col-md-4 col-lg-4">
                    <div className="panel panel-link-middle">
                      <div className="panel-heading text-center">
                        <strong className="panel-title">Categorias</strong>
                      </div>
                      <div className="panel-body">
                        <h4 className="text-center">
                          {this.state.categories}
                        </h4>
                      </div>
                    </div>
                  </div>

                  <div className="col-md-4 col-lg-4">
                    <div className="panel panel-link-after">
                      <div className="panel-heading text-center">
                        <strong className="panel-title">Autores</strong>
                      </div>
                      <div className="panel-body">
                        <h4 className="text-center">
                          {this.state.authors}
                        </h4>
                      </div>
                    </div>
                  </div>
                </Row>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    )
  }

  
}