/*
component Login - frontend link-panel

pagina inicial do painel, onde fazem login para acessar
*/
import React from 'react';
import api from '../services/api';

import { Fade } from 'reactstrap';

import './Login.css';
import './Default.css';

//imagens
import logo from '../assets/link-logo.png';
//import background from '../assets/back-mountain.png';
import background from '../assets/back-mountain-night.jpg';
//import background from '../assets/back-cloud.jpg';
//import background from '../assets/back-planet.jpg';
//import background from '../assets/back-city.jpg';
//import background from '../assets/back-moonlit.jpg';
//import background from '../assets/back-aqua.jpg';
//import background from '../assets/back-seasons.jpg';
//import background from '../assets/back-kokoye-beach.jpg';
//end imagens

export default class Login extends React.Component{
  
  constructor(props){
    super(props);

    this.state = {
      default: '',
      noLogin: '',
      loading: 'Conectando...'
    };
    
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount(){
    //requisita o metodo defaultDb no backend
    api.get('/', )
    .then((res) => { 
      // console.log('didMount >', res.data)
      //se tem seta o state default
      this.setState({ default: res.data })
    })
    .catch(function(error){
      console.log(error)
    });

    //no didMount seta o fundo com a variavel background
    const back = document.body;
    
    back.style.background = `url('${background}') 100% no-repeat`;
    back.style.backgroundPosition = "top center";
    //algumas imagens sao grandes entao precisa ajustar o posicionamento
    if (background.includes("kokoye")){
      back.style.backgroundPosition="center";
    }
  }

  //handleSubmit lida com os dados do login
  handleSubmit(event){
    //previne comportamento padrão de redirecionar
    event.preventDefault();
    //div com mensagem de usuario ou senha incorretos
    const alert = document.getElementById('link-alert');
    //const data pega o value de todos os campos do form
    const data = new FormData(event.target);
    //requisita o metodo login passando usuario e senha
    api.post('/login', {
      "username": data.get("username"),
      "password": data.get("password")

    })
    .then((res) => {       
      // console.log(res)
      //se tem key msg entao nao encontrou usuario
      if ("msg" in res.data){
        this.setState({ noLogin: res.data.msg });
        //se nao encontrou, mostra o alerta
        alert.style.display = "block";
        //alerta some após 5 segundos
        setTimeout(function() {
          alert.style.display = "none";
        }, 5000);
      }
      else{
        //se encontrou, faz um base64 do usuario e banco
        //redireciona para o componente Main, que irá 
        //fazer o decode desses dados e prosseguir
        var id = btoa(res.data._id + "_" + res.data.database);
        window.location.assign(`main/${id}`);
      }
    })
    .catch((error) => {
      console.log(error);
    });
  }

  render(){
    let Form;
    //se setou o banco default mostra o form
    if (this.state.default){
      Form = (
        <form onSubmit={this.handleSubmit}>
          <div className="spacement" />

          <img src={logo} alt="logo"/><br/>

          <input 
            placeholder="Digite seu usuário"
            name="username"
          /> 
          <input type="password"
            placeholder="Digite sua senha"
            name="password"
          /><br/>

          <div id="link-alert" className="alert alert-light" 
            style={{display: 'none'}} role="alert">
            {this.state.noLogin}
          </div>

          <button type="submit">
            Entrar
          </button>
        </form>
      )
    }else{
      //se nao setou mostra o badge 'conectando...'
      Form = (
        <>
          <div className="spacement"/>
          <div id="link-alert" className="alert alert-light" role="alert">
            {this.state.loading}
          </div>
        </>
      )
    }

    return (
      <div>
        <Fade in={this.state.fadeIn} tag="div">
          <div className="login-container">
            {/* retorna o form (ou nao) */}
            {Form}
          </div>
        </Fade>
      </div>
    )
  }
}