/*
component Categories - frontend link-panel

aqui buscamos as categorias passando o banco do usuario
como parametro, que renderiza o retorno na tabela
*/

import React from "react";
import api from "../services/api";

import {
  Container,
  Row,
  Col
} from "reactstrap";
import "./Dashboard.css";

import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
//pega o db do localStorage front
var db = localStorage.getItem("db");

export default class Categories extends React.Component {
  constructor(props) {
    super(props);

    this.userId = window.location.pathname.substring(6, 30);
    this.state = {
      categories: []
    };

    this.fetchData = this.fetchData.bind(this);
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db) {
      window.location.href = "/";
    } else {
      this.fetchData();
      // this.timer = setInterval(() => this.fetchData, 50)
    }
  }

  //fetchData requisita os categoriess do usuario
  fetchData() {
    // console.log(db)
    api
      .post("/panel/categories", { db })
      .then(res => {
        //se tem registro insere eles no state.categoriess
        this.setState({
          categories: res.data
        });
      })
      .catch(function(error) {
        console.log("Error getting the categoriess:", error);
      });
  }

  render() {
    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="8">
              <div className="spacement" />
              <h2>Categorias</h2><br/>
              {/* se tem categoriess mostra eles na tabela */}
              {!this.state.categories.msg ? (
                <div>
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <th scope="col">Ordem</th>
                        <th scope="col">Nome</th>
                        <th scope="col">Criado em</th>
                        <th scope="col">Atualizado em</th>
                        <th scope="col">Opções</th>
                      </tr>
                    </thead>

                    <tbody>
                      {this.state.categories.map(category => (
                        <tr key={category._id}>
                          <th>{category.order}</th>
                          <td>{category.name}</td>
                          <td>{category.createdAt}</td>
                          <td>{category.updatedAt}</td>
                          <td>
                            <div className="btn-group">
                              <button type="button" className="btn btn-info" 
                                data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i className="fa fa-ellipsis-v" aria-hidden="true"></i>
                              </button>
                              <div className="dropdown-menu dropdown-menu-right">
                                <a className="dropdown-item" 
                                  href={`/main/${this.userId}/categories/${category._id}`}>
                                  Editar
                                </a>
                                <div className="dropdown-divider"></div>
                                {/* <a className="dropdown-item" href="#">Link separado</a> */}
                              </div>
                            </div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                /*se não tem mostra esse html*/
                <h1>sem categorias :(</h1>
              )}
            </Col>

            <Col md="2" style={{
              borderLeft: 'solid 1px #e7e7e7',
              marginTop: '5em'
            }}>              
              <div style={{position: 'fixed'}}>
                <ul className="list-group">
                  <li className="list-group-item"><h5>Ações</h5></li>
                  
                  <li className="list-group-item list-group-item-light">
                    <a href={`/main/${this.userId}/category/new`} className="badge badge-success lg-txt">
                      <i className="fa fa-plus" aria-hidden="true"></i>
                      <span className="space-icon">Adicionar categoria</span>
                    </a>
                  </li>
                  <li className="list-group-item list-group-item-light">
                    <a href="google.com" className="badge badge-light lg-txt">
                      <i className="fa fa-print" aria-hidden="true"></i>
                      <span className="space-icon">Exportar registros</span>
                    </a>
                  </li>
                </ul>
              </div>              
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
