/*
component AddPost - frontend link-panel

tela para adicionar um novo post
*/

import React from "react";
import api from "../../services/api";

import {
  Container,
  Row,
  Col
} from "reactstrap";
import "../Default.css";

import Header from "../../components/Header";
import Sidebar from "../../components/Sidebar";
import CKEditor from 'ckeditor4-react';
CKEditor.editorUrl = "https://cdn.ckeditor.com/4.13.0/full/ckeditor.js"
//pega o db do localStorage front
var db = localStorage.getItem("db");

export default class AddPost extends React.Component {
  constructor(props) {
    super(props);

    // this.timer = null
    this.state = {
      categories: [],
      status: '',
      title: '',
      brief: '',
      category: '',
      content: '',
      return: '',
      update: false
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.onEditorChange = this.onEditorChange.bind( this );
    this.fetchCategories = this.fetchCategories.bind(this);
    this.Cancel = this.Cancel.bind(this);
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db) {
      window.location.href = "/";
    }else{
      this.fetchCategories();      
    }
  }

  fetchCategories(){
    api
      .post(`/panel/categories`, { db })
      .then((res) => {       
        this.setState({ 
          categories: res.data 
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  //handleChange atualiza o valor do campo conforme digita
  //depois manda o valor final do campo para atualizar no backend
  
  onEditorChange( evt ) {
    this.setState( {
        content: evt.editor.getData()
    } );
  }


  //handleSubmit pega todos os campos do form e 
  //manda atualizar o que tiver alterado
  handleSubmit(event){
    //previne comportamento padrão de redirecionar
    event.preventDefault();
    //div com mensagem de usuario ou senha incorretos
    const alert = document.getElementById('link-alert');
    const btnSave = document.getElementById('save-post');
    const btnCancel = document.getElementById('cancel-post');
    //buscando campos do form
    var form = document.getElementById("form-add-post");
    var elements = form.elements;
    
    //const data pega o value de todos os campos do form
    const data = new FormData(event.target);
    //requisita o metodo login passando usuario e senha
    api.post(`/panel/post/new`, { 
      db,
      "status": data.get("status"),
      "title": data.get("title"),
      "category": data.get("category"),
      "brief": data.get("brief"),
      "content": data.get("content")
    })
    .then((res) => {  
      //se tem key msg entao nao encontrou o post
      if (res.data.error){
        this.setState({ return: res.data.error });
        //se nao encontrou, mostra o alerta
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-danger");
        //alerta some após 5 segundos
        setTimeout(function() {
          alert.style.display = "none";
        }, 5000);
      }
      else if (res.data.msg){
        this.setState({ update: true }) 
        this.setState({ return: res.data.msg });
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-success");
        btnSave.style.display = "none";
        btnCancel.style.display = "none";

        //percorre os campos do form para desabilitar apos salvar
        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        const path = window.location.pathname.substring(0, 30)
        // console.log(path)
        setTimeout(function() {
          window.location.href = path + '/posts'
        }, 3000);       
      }
    })
    .catch((error) => {
      console.log(error);
    });
    // console.log(this.state.content)
  }


  handleChange(event) {
    var eventName = event.target.name

    if (eventName === 'status'){
      this.setState({
        status: event.target.value
      });
    }else if (eventName === 'title'){
      this.setState({
        title: event.target.value
      });
    }else if (eventName === 'category'){
      this.setState({
        category: event.target.value
      });
    }else if (eventName === 'brief'){
      this.setState({
        brief: event.target.value
      });
    }else if (eventName === 'content'){
      this.setState({
        content: event.target.value
      });
    }
  }

  //cancel ira cancelar o novo post e
  //retornar para a lista
  Cancel(){
    const btnSave = document.getElementById('save-post');
    const btnCancel = document.getElementById('cancel-post');
    var form = document.getElementById("form-add-post");
    var elements = form.elements;

    btnSave.style.display = "none";
    btnCancel.style.display = "none";

    for (var i = 0, len = elements.length; i < len; ++i) {
      elements[i].readOnly = true;
      elements[i].disabled = true;
    }

    const path = window.location.pathname.substring(0, 30)
    // console.log(path)
    window.location.href = path + '/posts'
  }


  render() {

    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="10">
              <div className="spacement" />
                <>
                
                <div id="link-alert"
                  style={{display: 'none'}} role="alert">
                  {this.state.return ? this.state.return : ''}
                </div>
                <Row>
                  <Col md="10">
                    <h3> novo post </h3>
                  </Col>
                </Row><hr/><br/>

                <form id="form-add-post" className="form-container" onSubmit={this.handleSubmit}>
                  <Row>
                    <Col md="10">
                      <Row>
                        <Col md="4">
                          <div className="form-group row">
                            <label htmlFor="inputStatus" className="col-sm-4 col-form-label">
                              Status</label>
                            <div className="col-sm-10">
                              <select className="form-control" required name="status" 
                                id="inputStatus">
                                <option value='0'>Rascunho</option>
                                <option value='1'>Publicado</option>
                              </select>
                            </div>
                          </div>
                        </Col>

                        <Col md="8">
                          <div className="form-group row">
                            <label htmlFor="inputTitle" className="col-sm-4 col-form-label">
                              Título</label>
                            <div className="col-sm-10">
                              <input type="text" required className="form-control" name="title"
                                id="inputTitle" placeholder="É o nome principal do post" />
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row>
                        <Col md="4">
                          <div className="form-group row">
                            <label htmlFor="inputCategory" className="col-sm-4 col-form-label">
                              Categoria</label>
                            <div className="col-sm-10">
                              <select className="form-control" required name="category" id="inputCategory">
                                {this.state.categories.map((category) => 
                                  <option key={category.order} 
                                    value={category._id}>{category.name}</option>
                                )}
                              </select>
                            </div>
                          </div>
                        </Col>

                        <Col md="8">
                          <div className="form-group row">
                            <label htmlFor="inputBrief" className="col-sm-4 col-form-label">
                              Resumo</label>
                            <div className="col-sm-10">
                              <textarea style={{width:'500px'}} required maxLength="200" 
                                name="brief" id="inputBrief" rows="6"></textarea>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row>
                        <Col md="12">
                          {/* <Froala content={this.state.content}></Froala> */}
                          <div className="form-group row">
                            <label htmlFor="inputContent" className="col-sm-4 col-form-label">
                              Conteúdo</label>
                              <div className="col-sm-10">
                                <CKEditor
                                  // data={this.state.con}
                                  onChange={this.onEditorChange}
                                  onBeforeLoad={ ( CKEDITOR ) => ( CKEDITOR.disableAutoInline = true ) }
                                />

                                <input type="hidden" name="content" id="inputContent"
                                  value={this.state.content} />
                            </div>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  
                  <Row>
                    <Col md="7">
                      <button id="cancel-post" className="cancel" onClick={this.Cancel}>
                        <i className="fa fa-arrow-left" aria-hidden="true"></i>
                        <span className="space-icon">Não salvar</span>
                      </button>
                    </Col>

                    <Col md="4">
                      <button id="save-post" type="submit">
                        <i className="fa fa-check" aria-hidden="true"></i>
                        <span className="space-icon">Confirmar alterações</span>                        
                      </button>
                    </Col>
                  </Row>                
                </form>
                </>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}