/*
component AddCategory - frontend link-panel

tela para adicionar uma nova categoria
*/

import React from "react";
import api from "../../services/api";

import {
  Container,
  Row,
  Col
} from "reactstrap";
import "../Default.css";

import Header from "../../components/Header";
import Sidebar from "../../components/Sidebar";
//pega o db do localStorage front
var db = localStorage.getItem("db");

export default class AddCategory extends React.Component {
  constructor(props) {
    super(props);

    // this.timer = null
    this.state = {
      return: '',
      update: false
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.Cancel = this.Cancel.bind(this);
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db) {
      window.location.href = "/";
    }
  }


  //handleSubmit pega todos os campos do form e 
  //manda atualizar o que tiver alterado
  handleSubmit(event){
    //previne comportamento padrão de redirecionar
    event.preventDefault();
    //div com mensagem de usuario ou senha incorretos
    const alert = document.getElementById('link-alert');
    const btnSave = document.getElementById('save-category');
    const btnCancel = document.getElementById('cancel-category');
    //buscando campos do form
    var form = document.getElementById("form-add-category");
    var elements = form.elements;
    
    //const data pega o value de todos os campos do form
    const data = new FormData(event.target);
    //requisita o metodo login passando usuario e senha
    api.post(`/panel/category/new`, { 
      db,
      "order": data.get("order"),
      "name": data.get("title"),
    })
    .then((res) => {  
      //se tem key msg entao nao encontrou o category
      if (res.data.error){
        this.setState({ return: res.data.error });
        //se nao encontrou, mostra o alerta
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-danger");
        //alerta some após 5 segundos
        setTimeout(function() {
          alert.style.display = "none";
        }, 5000);
      }
      else if (res.data.msg){
        this.setState({ update: true }) 
        this.setState({ return: res.data.msg });
        alert.style.display = "block";
        alert.setAttribute("class", "alert alert-success");
        btnSave.style.display = "none";
        btnCancel.style.display = "none";

        //percorre os campos do form para desabilitar apos salvar
        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        const path = window.location.pathname.substring(0, 30)
        // console.log(path)
        setTimeout(function() {
          window.location.href = path + '/categories'
        }, 3000);       
      }
    })
    .catch((error) => {
      console.log(error);
    });
  }

  //cancel ira cancelar o novo category e
  //retornar para a lista
  Cancel(){
    const btnSave = document.getElementById('save-category');
    const btnCancel = document.getElementById('cancel-category');
    var form = document.getElementById("form-add-category");
    var elements = form.elements;

    btnSave.style.display = "none";
    btnCancel.style.display = "none";

    for (var i = 0, len = elements.length; i < len; ++i) {
      elements[i].readOnly = true;
      elements[i].disabled = true;
    }

    const path = window.location.pathname.substring(0, 30)
    // console.log(path)
    window.location.href = path + '/categories'
  }


  render() {

    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="10">
              <div className="spacement" />
                <>
                
                <div id="link-alert"
                  style={{display: 'none'}} role="alert">
                  {this.state.return ? this.state.return : ''}
                </div>
                <Row>
                  <Col md="10">
                    <h3> nova categoria </h3>
                  </Col>
                </Row><hr/><br/>

                <form id="form-add-category" className="form-container" onSubmit={this.handleSubmit}>
                  <Row>
                    <Col md="10">
                      <Row>
                        <Col md="4">
                          <div className="form-group row">
                            <label htmlFor="inputOrder" className="col-sm-4 col-form-label">
                              Ordem</label>
                            <div className="col-sm-10">
                              <input type="text" required className="form-control" name="order"
                                id="inputOrder" placeholder="Posição" />
                            </div>
                          </div>
                        </Col>

                        <Col md="8">
                          <div className="form-group row">
                            <label htmlFor="inputTitle" className="col-sm-4 col-form-label">
                              Título</label>
                            <div className="col-sm-10">
                              <input type="text" required className="form-control" name="title"
                                id="inputTitle" placeholder="É o nome da categoria" />
                            </div>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  
                  <Row>
                    <Col md="7">
                      <button id="cancel-category" className="cancel" onClick={this.Cancel}>
                        <i className="fa fa-arrow-left" aria-hidden="true"></i>
                        <span className="space-icon">Não salvar</span>
                      </button>
                    </Col>

                    <Col md="4">
                      <button id="save-category" type="submit">
                        <i className="fa fa-check" aria-hidden="true"></i>
                        <span className="space-icon">Confirmar alterações</span>                        
                      </button>
                    </Col>
                  </Row>               
                </form>
                </>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
