/*
component Client - frontend link-panel

lista todos os clientes para quem tiver acesso admin
*/

import React from "react";
import api from "../services/api";

import { Container, Row, Col } from 'reactstrap';
import './Dashboard.css';

import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
//pega o db do localStorage front
var db = localStorage.getItem('db')

export default class Clients extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      clients: []
    };
  }

  componentDidMount() {
    //se nao esta autenticado
    if (!db){
      window.location.href="/"
    }else{
      //busca os clientes na base
      api.get("/panel/clients")
      .then(res => {
        //se encontrou seta eles no state.clients
        this.setState({ 
          clients: res.data 
        });
      })
      .catch(function(error){
        console.log('Error getting the clients:', error);
      });
    }
  }

  render() {

    return (
      <div>
        <Container fluid>
          <Header></Header>

          <Row>
            <Col md="2">
              <div className="spacement" />
              <Sidebar></Sidebar>
            </Col>

            <Col md="10">
              <div className="spacement" />
              {/* se tem clients mostra eles na tabela */}
              {!this.state.clients.msg ? (
                <div>
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <th scope="col">Domínio</th>
                        <th scope="col">Nome</th>
                        <th scope="col">Telefone</th>
                        <th scope="col">E-mail</th>
                        <th scope="col">Plano</th>
                        <th scope="col">Ciclo</th>
                        <th scope="col">Expira em</th>
                        <th scope="col">Status</th>
                        <th scope="col">Ações</th>
                      </tr>
                    </thead>

                    <tbody>
                      {this.state.clients.map((client) => 
                        <tr key={client._id}>
                          <th>
                            <a href={`http://${client.domain}`} 
                              target="_blank" rel="noopener noreferrer">
                              {client.domain}
                            </a>
                          </th>
                          <td>{client.name}</td>
                          <td>{client.phone}</td>
                          <td>{client.email}</td>
                          <td>{client.plan}</td>
                          <td>{client.cicle}</td>
                          <td>{client.expires}</td>
                          <td>{client.status}</td>
                          <td>
                            <div className="btn-group">
                              <button type="button" className="btn btn-info" 
                                data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i className="fa fa-ellipsis-v" aria-hidden="true"></i>
                              </button>
                              <div className="dropdown-menu dropdown-menu-right">
                                {/* <a className="dropdown-item" href="#">Alguma ação</a> */}
                                <div className="dropdown-divider"></div>
                                {/* <a className="dropdown-item" href="#">Link separado</a> */}
                              </div>
                            </div>
                          </td>
                        </tr>
                      )}                      
                    </tbody>
                  </table>
                </div>
              ) : (
                /*se não tem mostra esse html*/
                <h1>sem clientes :(</h1>
              )}
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
