/*
componente Sidebar - frontend link-panel

renderiza o menu lateral com as opções
enquanto nao entendemos o redux, vai no array fixo
*/
import React from 'react';

import '../pages/Default.css';
import '../pages/Dashboard.css';

const initialState = {
  components: [
    {
      id: '1',
      title: 'dashboard',
      icon: 'fa fa-tachometer',
      component: 'dashboard'
    },
    {
      id: '2',
      title: 'posts',
      icon: 'fa fa-file',
      component: 'posts'
    },
    {
      id: '3',
      title: 'categorias',
      icon: 'fa fa-bars',
      component: 'categories'
    },
    {
      id: '4',
      title: 'autores',
      icon: 'fa fa-users',
      component: 'authors'
    },
    {
      id: '99',
      title: 'minha conta',
      icon: 'fa fa-cogs',
      component: 'profile'
    }
  ]
};

export default class Sidebar extends React.Component{
    
  constructor(props){

    super(props);

    this.userId = window.location.pathname.substring(6, 30);
    //se for o usuario admin, adiciona opções
    if (this.userId === '5d9261850e850f140454f415'){
      initialState.components.push(
        {
          id: '7',
          title: 'clients',
          icon: 'fa fa-user',
          component: 'clients'
        }
      )

      initialState.components.splice(4, 1)
    }
  }

  render(){

    return(
      <div className="link-sidebar">
        <nav className="col-md-2 d-none d-md-block bg-light sidebar"><br/>
          <ul className="nav flex-column sidebar-ul">
            {initialState.components.map((component) => 
              <li className="nav-item" key={component.id}>
                <a href={`/main/${this.userId}/${component.component}`}>
                  <span className="nav-link">
                    <i className={component.icon} aria-hidden="true"></i>
                    <span className="space-icon">{component.title}</span>
                  </span>
                </a>
              </li>
            )}
          </ul>
        </nav>
      </div>
    )
  }
}
